.card-container {
  width: 10.5rem;
  height: 5.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  line-height: 0;
  padding: 0.5rem;
  border-radius: 10px;
  transition: 0.4s;
  cursor: pointer;
}

.card-container:hover {
  scale: 1.1;
}
